<template>
    <div class="monallog-channel">
        <m-line-area
            ref="lineArea" />
        <m-music-player
            :playlist="chOptions.playlist"
            :mode="{isRandom: true, isLoop: false}"
            v-if="turnOnAudio"
            @music-start="showMusicInfo"/>
        <div class="post-line">
            <m-text-bar
                class="line-text"
                :catch-phrase="encourage"
                @enter="postLine"
                v-model="line"/>
            <button
                class="line-button"
                @click="postLine"
                @mouseenter="hasCursor = true"
                @mouseleave="hasCursor = false">
                <transition name="fade-spacing"
                    mode="out-in">
                    <img src="../assets/line-btn-icon.png"
                        class="line-button-content line-button-icon"
                        v-if="!hasText && !hasCursor"
                        key="btn-img">
                    <span class="line-button-content line-button-text"
                        :style="counterColor"
                        v-else-if="!hasCursor"
                        key="btn-counter">
                        {{ lineCount }}
                    </span>
                    <span class="line-button-content line-button-text text-post"
                        v-else
                        key="btn-text">
                        SPEAK
                    </span>
                </transition>
            </button>
        </div>
    </div>
</template>

<script>
import MTextBar from '@/components/MTextBar.vue'
import MLineArea from '@/components/MLineArea.vue'
import MMusicPlayer from '@/components/MMusicPlayer.vue'

import axios from 'axios'
import io from 'socket.io-client'

export default {
    name: 'MonallogChannel',
    components: {
        'm-line-area': MLineArea,
        'm-text-bar': MTextBar,
        'm-music-player': MMusicPlayer
    },
    data () {
        return {
            socket: null,
            line: '',
            hasCursor: false,
            isSocketOn: false,
            chOptions: Object
        }
    },
    created () {
        this.socket = io(process.env.VUE_APP_SOCKET_SERVER)

        // subscribing event listeners
        this.socket.on('connect', () => {
            this.$refs.lineArea.pushNotice({
                msgCode: 1,
                msgType: 'notice',
                msg: 'socketio  .',
                timeout: 3000
            })

            this.socket.emit('join', { channel: this.$route.params.chId })

            this.isSocketOn = true
        })
        this.socket.on('disconnect', () => {
            this.$refs.lineArea.pushNotice({
                msgCode: 2,
                msgType: 'error',
                msg: 'socketio   .',
                timeout: 5000
            })
            this.isSocketOn = false
        })
        this.socket.on('line', (data) => {
            if (!document.hidden) { // don't get lines when page is inactive
                this.$refs.lineArea.enqueue(data.line)
            }
        })
        this.socket.on('news', (data) => { // DEBUG
            console.log(data)
        })
    },
    beforeRouteEnter (to, from, next) {
        const requestURL = process.env.VUE_APP_API_SERVER +
      'channels/' +
      to.params.chId

        axios.get(requestURL)
            .then(function (response) {
                console.log(response) //  !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!

                next(vm => { // channel view  
                    vm.chOptions = response.data.channelOptions

                    vm.$store.dispatch('turnOnBg')
                    vm.$store.dispatch('updateBgImg', { url: vm.chOptions.backgroundURL })
                })
            })
            .catch(function (error) {
                if (error.response && error.response.status === 404) next('404') //  channel 
                else { //   
                    next(error)
                }
            })
    },
    beforeRouteLeave (to, from, next) {
        if (this.isSocketOn) { this.socket.close() }

        this.$store.dispatch('turnOffBg')
        next()
    },
    computed: {
        encourage: function () {
            return 'Speak your line.'
        },
        lineCount: function () {
            return this.line.length
        },
        counterColor: function () {
            const gray = 200 - this.lineCount * 5
            return {
                color: `rgb(${gray}, ${gray}, ${gray})`
            }
        },
        hasText: function () {
            return this.lineCount !== 0
        },
        turnOnAudio: function () {
            return !(this.chOptions.playlist === undefined || this.chOptions.playlist.length === 0)
        }
    },
    methods: {
        postLine: function () {
            if (this.line && this.isSocketOn) { //   
                this.socket.emit('line', {
                    line: this.line,
                    channel: this.$route.params.chId, // placeholding
                    author: '' // also placeholoding
                })
            } else if (!this.line) { //  
                alert(' .')
            } else if (!this.isSocketOn) { // socketio  
                alert('   .\n anteater333@github   .')
            }
            this.line = ''
        },
        showMusicInfo: function (music) {
            const str = `${music.title} - ${music.by}`
            this.$refs.lineArea.enqueue(str)
        }
    }
}
</script>

<style scoped>
.monallog-channel {
    position: absolute;

    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
}

.line-area {
    position: relative;
    flex: 1;
}

.music-player {
    position: absolute;
    z-index: 5;

    bottom: 50px;
    right: 0;
}

.post-line {
    margin-top: auto;
    margin-bottom: 0.5%;

    display: flex;

    opacity: 0.75;
}

.line-text {
    flex: 1;

    margin-left: 1%;
}

.line-button {
    box-sizing: content-box;

    margin-left: 1%;
    margin-right: 1%;
    padding: 0px;

    display: flex;
    justify-content: center;

    border: 4px solid var(--white);
    border-radius: 10px;

    width: 42px;
    height: 42px;

    background-color: var(--white);
    background-position: center center;
    background-repeat: no-repeat;

    overflow: hidden;

    cursor: pointer;

    transition: 0.5s;
}

.line-button:hover {
    width: 84px;
}

.line-button:focus {
    outline: none;
}

.line-button-icon {
    flex: 0 0 0;
}

.line-button-text {
    font-size: 18px;
    text-align: center;
    line-height: 42px;
}

.text-post {
    color: var(--black);
    font-weight: bold;
}

</style>
